import React from "react"
import LengthWidth from "./LengthWidth.js"
import Unit from "./Unit.js"
import BoxSize from "./BoxSize.js"

class View extends React.Component
{
     render()
    {
      return( <div>
                   <h1>Tile Sizing Calculator</h1>
                   
                   <table>
                         <tbody>
                                  <tr>
                                        <td>Tile Size Unit:<Unit />
                                            <br/><br/>
                                            <LengthWidth />                                    
                                        </td>
                                  </tr>
                                  <tr>                                      
                                         <td>Area Size Unit:<Unit />
                                             <br/><br/>
                                             <LengthWidth />                      
                                         </td>
                                  </tr>
                                  <tr>                                      
                                         <td>Box Size: <BoxSize /></td>
                                  </tr>
                         </tbody>
                   </table>
             </div>)
    }   
}
export default View 