import React from "react"
class LengthWidth extends React.Component 
{
  constructor()
  {
      super();
  }  
  render()  
  { return (
      <span>
        length : <input type="textbox" size="5" maxLength="5"></input>
        width : <input type="textbox" size="5" maxLength="5"></input>
    </span>       
   ) }    
}
export default LengthWidth